<template>
    <div class="card" style="margin-top: 20px">
        <div class="card-header bg-white header-elements-inline">
            <h6 class="card-title">User List <span class="text-danger" v-if="(page_message.length > 0)">({{page_message}})</span></h6>
            <div class="header-elements">
                <div class="list-icons">
                    <a href="#" data-toggle="modal" data-target="#modal_iconified" class="list-icons-item"><i class="icon-user-plus"></i></a>
                </div>
            </div>
        </div>
        <div class="card-body" :style="{ 'height': `${windowHeight - 170}px`}">
            <div class="table-responsive">
                <table class="table table-xs">
                    <thead>
                    <tr>
                        <th :style="{ 'width': `20px`}">#</th>
                        <th :style="{ 'width': `${windowWidth - 130 - 820}px`}">User Name</th>
                        <th :style="{ 'width': `200px`}">Store Name</th>
                        <th :style="{ 'width': `170px`}">Created On</th>
                        <th :style="{ 'width': `400px`}">Actions</th>
                    </tr>
                    </thead>
                    <tbody :style="{ 'height': `${windowHeight - 220}px`, 'overflow-y':'auto' }">
                    <tr v-for="(dta, index) in data" v-bind:key="index">
                        <td :style="{ 'width': `20px` }">{{dta[0]}}</td>
                        <td :style="{ 'width': `${windowWidth - 130 - 820}px` }">{{dta[1]}}</td>
                        <td :style="{ 'width': `200px`}">{{dta[2]}}</td>
                        <td :style="{ 'width': `170px`}">{{dta[3]}}</td>
                        <td :style="{ 'width': `400px`}">
                            <div v-if="(dta[4].length > 0)" class="btn-group-sm">
                                <button type="button" class="btn btn-danger" v-on:click="removeUser(dta[4])">Remove</button>
                                <button type="button" class="btn btn-warning" v-on:click="setDefaultUserId(dta[4])" data-toggle="modal" data-target="#modal_user_password_change">Change Password</button>
                                <a :href="('/user_permission/'+dta[4])" type="button" class="btn btn-primary">Change Permission</a>
                            </div>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div id="modal_iconified" class="modal fade" tabindex="-1">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title"><i class="icon-user-plus mr-2"></i> Create User</h5>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>
                    <div class="modal-body">
                        <div v-if="(userCreationErrorMessage.length > 0)" class="alert alert-warning alert-dismissible">
                            <button type="button" class="close" data-dismiss="alert"><span>×</span></button>
                            <span class="font-weight-semibold">Warning!</span> {{userCreationErrorMessage}}
                        </div>
                        <div class="form-group row">
                            <label class="col-form-label col-sm-3">User Name</label>
                            <div class="col-sm-9">
                                <input type="text" placeholder="Type User Name" class="form-control" v-model="enteredUserName">
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-form-label col-sm-3">User Password</label>
                            <div class="col-sm-9">
                                <input type="password" placeholder="Type User Password" class="form-control" v-model="enteredUserPassword">
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button class="btn btn-link" data-dismiss="modal"><i class="icon-cross2 font-size-base mr-1"></i> Close</button>
                        <button class="btn bg-primary" v-on:click="createUser()"><i class="icon-checkmark3 font-size-base mr-1"></i> Save</button>
                    </div>
                </div>
            </div>
        </div>

        <div id="modal_user_password_change" class="modal fade" tabindex="-1">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title"><i class="icon-cog2 mr-2"></i> Update Password</h5>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>
                    <div class="modal-body">
                        <div v-if="(userCreationErrorMessage.length > 0)" class="alert alert-warning alert-dismissible">
                            <button type="button" class="close" data-dismiss="alert"><span>×</span></button>
                            <span class="font-weight-semibold">Warning!</span> {{userCreationErrorMessage}}
                        </div>
                        <div class="form-group row">
                            <label class="col-form-label col-sm-3">New Password</label>
                            <div class="col-sm-9">
                                <input type="password" placeholder="Type New Password" class="form-control" v-model="enteredUserPassword">
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button class="btn btn-link" data-dismiss="modal"><i class="icon-cross2 font-size-base mr-1"></i> Close</button>
                        <button class="btn bg-primary" v-on:click="resetPassword()"><i class="icon-checkmark3 font-size-base mr-1"></i> Save</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "UserList",
        components: {},
        data() {
            return {
                userCreationErrorMessage:'',
                page_message:'',
                enteredUserName:'',
                enteredUserPassword:'',
                windowHeight: 0,
                windowWidth: 0,
                defaultUserId:'',
                data: [],
            }
        },
        mounted() {
            this.windowHeight = window.innerHeight
            this.windowWidth = window.innerWidth
            window.addEventListener('resize', () => {
                this.windowHeight = window.innerHeight
                this.windowWidth = window.innerWidth
            })
            this.$parent.pageTitle = "User List"
            this.$parent.showPageTitle = false
            this.loadPageData();
        },
        methods: {
            loadPageData(){
                this.page_message = 'Reloading Table.....'
                axios
                    .get(this.$parent.apiEndPoint+"user", {
                        headers: {
                            Authorization: 'Basic '+REDIS_TOKEN
                        }
                    })
                    .then(response => {
                        this.page_message = ''
                        this.data = response.data.data
                    })
                    .catch(error => {
                        if (error.response) {
                            switch (error.response.status)
                            {
                                case 401:
                                    this.$parent.makeForceLogout()
                                    break;
                            }
                        }
                    });
            },
            removeUser(user_id){
                if(confirm("Are you sure?"))
                {
                    axios
                        .delete(this.$parent.apiEndPoint+"user/"+user_id, {
                            headers: {
                                Authorization: 'Basic '+REDIS_TOKEN
                            }
                        })
                        .then(response => {
                            this.page_message = 'User Removed Successfully. Reloading User Table....'
                            this.loadPageData();
                        })
                        .catch(error => {
                            if (error.response) {
                                switch (error.response.status)
                                {
                                    case 401:
                                        this.$parent.makeForceLogout()
                                        break;
                                }
                            }
                        });
                }
            },
            setDefaultUserId(uid){
                this.defaultUserId = uid;
            },
            resetPassword(){
                $('#modal_user_password_change').modal('hide');
                axios
                    .patch(this.$parent.apiEndPoint+"user/"+this.defaultUserId, {
                        password: this.enteredUserPassword
                    },{
                        headers: {
                            Authorization: 'Basic '+REDIS_TOKEN
                        },
                    })
                    .then(response => {
                        this.page_message = 'User Updated Successfully. Reloading User Table....'
                        this.loadPageData();
                    })
                    .catch(error => {
                        if (error.response) {
                            switch (error.response.status)
                            {
                                case 401:
                                    this.$parent.makeForceLogout()
                                    break;
                                case 406:
                                    console.log(error.response)
                                    this.userCreationErrorMessage = error.response.data.message.join(",")
                                    break;
                            }
                            $('#modal_user_password_change').modal('show');
                            this.page_message = ''
                        }
                    });

                this.page_message = 'Please Wait. We Are Updating User .....'
            },
            createUser(){
                $('#modal_iconified').modal('hide');

                axios
                    .post(this.$parent.apiEndPoint+"user", {
                        user_name: this.enteredUserName,
                        password: this.enteredUserPassword
                    },{
                        headers: {
                            Authorization: 'Basic '+REDIS_TOKEN
                        },
                    })
                    .then(response => {
                        this.page_message = 'User Created Successfully. Reloading User Table....'
                        this.loadPageData();
                    })
                    .catch(error => {
                        if (error.response) {
                            switch (error.response.status)
                            {
                                case 401:
                                    this.$parent.makeForceLogout()
                                    break;
                                case 406:
                                    console.log(error.response)
                                    this.userCreationErrorMessage = error.response.data.message.join(",")
                                    break;
                            }
                            $('#modal_iconified').modal('show');
                            this.page_message = ''
                        }
                    });

                this.page_message = 'Please Wait. We Are Creating User .....'
            }
        }
    }
</script>

<style scoped>
    @import '~handsontable/dist/handsontable.full.css';
    table {
        width: 100%;
    }
    thead, tbody, tr, td, th { display: block; }
    tr:after {
        content: ' ';
        display: block;
        visibility: hidden;
        clear: both;
    }
    thead th {
        height: 30px;
        /*text-align: left;*/
    }
    thead {
        /* fallback */
    }
    tbody td, thead th {
        width: 10%;
        float: left;
    }
</style>
